<template>
  <div class="corporate-culture">
    <no-enterprise v-if="mtsCatalog.length === 0 && !mtsCatalog._id" />
    <page-plate v-if="mtsCatalog.length" :mts-catalog="mtsCatalog" />
    <course-catalogue
      v-if="mtsCatalog.category === 'series_lesson'"
      :catalog="mtsCatalog"
      :list="list"
    />
    <course-detail
      :course-detail="mtsCatalog"
      v-if="mtsCatalog.category === 'single_lesson'"
    />
  </div>
</template>

<script>
import NoEnterprise from './components/NoEnterprise'
import PagePlate from './components/PagePlate'
import CourseCatalogue from './CourseCatalogue'
import CourseDetail from './CourseDetail'
import { getCourseDetail } from '@/api/learningAdmin'
export default {
  components: {
    NoEnterprise,
    PagePlate,
    CourseCatalogue,
    CourseDetail
  },
  data() {
    return {
      list: [],
      mtsCatalog: {}
    }
  },
  created() {
    this.getCourseDetail()
  },
  methods: {
    async getCourseDetail() {
      const { data, code } = await getCourseDetail({
        lesson_column: this.$route.params.name
      })
      if (code === 20000 && data.result) {
        this.mtsCatalog = data.mtsCatalog
        this.list = data.list
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (vm.$route.params.name === 'corporate_culture')
        to.meta.title = '企业文化'
      if (vm.$route.params.name === 'service_responsibilities')
        to.meta.title = '健康管理师'
      if (vm.$route.params.name === 'workflow') to.meta.title = '工作流程'
      if (vm.$route.params.name === 'professional_knowledge')
        to.meta.title = '业务能力知识'
    })
  }
}
</script>

<style scoped lang="scss">
.corporate-culture {
  height: 100%;
  img {
    max-width: 100%;
  }
}
</style>
